<template>
    <div class="consume-page">
        <el-radio-group v-model="platform" @change="changePlatform">
            <el-radio :label="0">推锐</el-radio>
            <el-radio :label="1">点锐</el-radio>
            <el-radio :label="2">要琪</el-radio>
            <el-radio :label="3">一飞</el-radio>
            <el-radio :label="4">有盟</el-radio>
            <el-radio :label="5">无忧</el-radio>
        </el-radio-group>
        <el-table border :data="dataList" style="width: 100%; margin-top: 30px;">
            <el-table-column header-align="center" align="center" prop="time" label="时间" width="100"></el-table-column>
            <el-table-column header-align="center" align="center" prop="adId" label="广告Id"></el-table-column>
            <el-table-column header-align="center" align="center" prop="adName" label="广告名称" width="100"></el-table-column>
            <el-table-column header-align="center" align="center" prop="deliveryType" label="投放平台"></el-table-column>
            <el-table-column header-align="center" align="center" prop="releaseData" label="投放数据"></el-table-column>
            <el-table-column header-align="center" align="center" prop="billingMethod" label="结算方式"></el-table-column>
            <el-table-column header-align="center" align="center" prop="consume" label="消耗" width="100"></el-table-column>
            <el-table-column header-align="center" align="center" prop="bannerConsume" label="横幅消耗"></el-table-column>
            <el-table-column header-align="center" align="center" prop="screenConsume" label="插屏消耗"></el-table-column>
            <el-table-column header-align="center" align="center" prop="clickConsume" label="点击消耗"></el-table-column>
            <el-table-column header-align="center" align="center" prop="showCount" label="PV"></el-table-column>
            <el-table-column header-align="center" align="center" prop="clickCount" label="UV"></el-table-column>
            <el-table-column header-align="center" align="center" prop="clickRate" label="点击率"></el-table-column>
            <el-table-column header-align="center" align="center" prop="accounts" label="结算数"></el-table-column>
            <el-table-column header-align="center" align="center" prop="delivery" label="日投放额"></el-table-column>
            <el-table-column header-align="center" align="center" prop="amount" label="账户余额"></el-table-column>
        </el-table>
    </div>
</template>

<script>
import Axios from 'axios';
export default {
    data() {
        return {
            platform: 0,
            dataList: [],
            tuirui: [],
            dianrui: [],
            yaoqi: [],
            yifei: [],
            youmeng: [],
            wuyou: [],
        }
    },
    mounted() {
        this.getData()
    },
    methods: {
        getData() {
            Axios({
                url: '/consume',
                method: 'get'
            }).then(res => {
                let dataArr = res.data.data
                for(let i = 0; i < dataArr.length; i ++) {
                    let item = dataArr[i]
                    switch (item.platform) {
                        case 0:
                            this.tuirui.push(item);
                        break;
                        case 1:
                            this.dianrui.push(item);
                        break;
                        case 2:
                            this.yaoqi.push(item);
                        break;
                        case 3:
                            this.yifei.push(item);
                        break;
                        case 4:
                            this.youmeng.push(item);
                        break;
                        case 5:
                            this.wuyou.push(item);
                        break;
                    }
                }
                
            })
        },
        changePlatform() {
            switch (this.platform) {
                case 0:
                    this.dataList = this.tuirui
                break;
                case 1:
                    this.dataList = this.dianrui
                break;
                case 2:
                    this.dataList = this.yaoqi
                break;
                case 3:
                    this.dataList = this.yifei
                break;
                case 4:
                    this.dataList = this.youmeng
                break;
                case 5:
                    this.dataList = this.wuyou
                break;
            }
        }
    }
}
</script>




