<template>
    <div id="app">
        <el-menu :default-active="onRoutes" mode="horizontal"  @select="handleSelect" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
            <template v-for="item in navlist">
				<el-menu-item :index="item.index" :key="item.index"> 
					<span slot="title">{{item.title}}</span>
				</el-menu-item>
			</template>
        </el-menu>
        <div class="content">
            <transition name="move" mode="out-in">
                <keep-alive>
                    <router-view />
                </keep-alive>
            </transition>
        </div>
    </div>
</template>

<script>
export default {
    name: 'App',
    data() {
        return {
            navlist: [{
				index: "/statistics",
				title: "小说平台统计"
			}, {
				index: "/statistics/consume",
				title: "小说消耗统计"
            }],
            onRoutes: '/statistics'
        }
    },
    methods: {
        handleSelect(key, keyPath) {
            this.$router.replace({ path: key })
        }
    }

}
</script>

<style>
* {
    margin: 0;
    padding: 0;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    text-decoration: none;
}

ul, ol {
    list-style: none;
}

input, textarea {
    outline: none;
}

img {
    border: 0;
}

.fl {
    float: left;
}

.fr {
    float: right;
}

#app {
    width: 1200px;
    margin: auto;
}

.inputtxt {
    width: 220px;
}

.btn-container {
    width: 200px;
    margin: auto;
}
.content {
    margin-top: 30px;
}
</style>



