import Vue from 'vue'
import Router from 'vue-router'
import indexPage from '@/views/indexPage'
import consumePage from '@/views/consumePage'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/statistics',
            component: indexPage,
            meta: {
                title: '小说平台数据'
            }
        },
        {
            path: '/statistics/consume',
            component: consumePage,
            meta: {
                title: '小说消耗数据'
            }
        }
    ]
})



